import userInfoBtn from '@/components/common/userInfoBtn.vue'
import uniSearchBar from '@/components/uni-search-bar/uni-search-bar.vue'
import Map from '../../js_sdk/fx-openMap/openMap.js'
import uCharts from '../../components/u-charts/u-charts.js';
import loadRefresh from '@/components/load-refresh/load-refresh.vue'
import {
	mapGetters
} from 'vuex'
var _self;
var canvaPie = null;
export default {
	components: {
		userInfoBtn,
		uniSearchBar,
		loadRefresh,
		// Map
	},
	data() {
		return {
			src: 'https://cdn.uviewui.com/uview/example/fade.jpg',
			list: [{
				text: '点赞',
				color: 'blue',
				fontSize: 28
			}, {
				text: '分享'
			}, {
				text: '评论'
			}],
			show: true,
			currPage: 1, // 当前页码
			totalPage: 1, // 总页数
			idolName: "",
			logout: false,
			newNickname: "",
			idolInfo: {},
			starUrl: '',
			background: ['color1', 'color2', 'color3'],
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 500,
			id: 0, // 使用 marker点击事件 需要填写id
			title: 'map',
			latitude: 39.909,
			longitude: 116.39742,
			covers: [{
				latitude: 39.909,
				longitude: 116.39742,
			}],
			cWidth: '',
			cHeight: '',
			pixelRatio: 1,
			serverData: '',
		}
	},
	computed: {
		...mapGetters([
			'userInfo'
		])
	},

	filters: {
		formatUserName(val) {
			console.log(val)
			if (val) {
				return val
			} else {
				console.log(2222)
				return 'uni-小程序'
			}
		},
		formatAvatarUrl(val) {
			if (val) {
				return val
			} else {
				// return require('@/static/images/common/default-avatar.png')
			}
		},
	},
	onShow() {},
	onLoad() {
		_self = this;
		this.cWidth = uni.upx2px(750);
		this.cHeight = uni.upx2px(500);
		this.getServerData();
	},
	methods: {
		// 上划加载更多
		loadMore() {
			console.log('loadMore')
			// 请求新数据完成后调用 组件内loadOver()方法
			// 注意更新当前页码 currPage
			this.$refs.loadRefresh.loadOver()
		},
		// 下拉刷新数据列表
		refresh() {
			console.log('refresh')
		},
		getServerData() {
			uni.request({
				url: 'https://www.ucharts.cn/data.json',
				data: {},
				success: function(res) {
					console.log(res.data.data)
					let Pie = {
						series: []
					};
					//这里我后台返回的是数组，所以用等于，如果您后台返回的是单条数据，需要push进去
					Pie.series = res.data.data.Pie.series;
					_self.showPie("canvasPie", Pie);
				},
				fail: () => {
					_self.tips = "网络错误，小程序端请检查合法域名";
				},
			});
		},
		showPie(canvasId, chartData) {
			canvaPie = new uCharts({
				$this: _self,
				canvasId: canvasId,
				type: 'pie',
				fontSize: 11,
				legend: {
					show: true
				},
				background: '#FFFFFF',
				pixelRatio: _self.pixelRatio,
				series: chartData.series,
				animation: true,
				width: _self.cWidth * _self.pixelRatio,
				height: _self.cHeight * _self.pixelRatio,
				dataLabel: true,
				extra: {
					pie: {
						lableWidth: 15
					}
				},
			});
		},
		touchPie(e) {
			canvaPie.showToolTip(e, {
				format: function(item) {
					return item.name + ':' + item.data
				}
			});
		},
		goMap() {
			uni.getLocation({
				type: 'gcj02',
				success: (res) => {
					Map.openMap(res.latitude, res.longitude, "北客站")
					console.log('当前位置的经度：' + res.longitude);
					console.log('当前位置的纬度：' + res.latitude);
				}
			});
		},
		input(data) {
			console.log(data)
		},
		search(data) {
			console.log(data)
		},
		logoutFansTeam() {
			this.idolName = this.userInfo.starName;
			this.logout = true;
		},
		onGetAuthData() {
			//获取授权成功后的数据
			console.log(3333)
		},
		changeIndicatorDots(e) {
			this.indicatorDots = !this.indicatorDots
		},
		changeAutoplay(e) {
			this.autoplay = !this.autoplay
		},
		intervalChange(e) {
			this.interval = e.target.value
		},
		durationChange(e) {
			this.duration = e.target.value
		}
	}
}
